import axios from 'axios'
import { Message } from 'element-ui'
import router from '../router/index'
// 超时时间
// axios.defaults.timeout = 5000
// http请求拦截器
// var loadinginstace
// axios.interceptors.request.use(config => {
//   // element ui Loading方法
//   loadinginstace = Loading.service({ fullscreen: true })
//   return config
// }, error => {
//   loadinginstace.close()
//   Message.error({
//     message: '加载超时'
//   })
//   return Promise.reject(error)
// })
// http响应拦截器
axios.interceptors.response.use(data => { // 响应成功关闭loading
  // loadinginstace.close()
  // console.log('jieguo', data)
  return data
}, error => {
  // loadinginstace.close()
  // console.log('cuowu', error)
  if (error.response) {
    var status = error.response.status.toString()
    console.log('转转类型之后', typeof (status))
    console.log('111', status)
    if (status === '401') {
      router.replace({
        path: 'login'
      })
      Message.error({
        message: '用户登录过期，请重新登录'
      })
    } else if (status === '500') {
      Message.error({message: '服务器在尝试处理请求时发生内部错误'})
    } else {
      Message.error({message: '未知错误'})
    }
  }
  return Promise.reject(error)
})

export default axios



