<template>
  <div class="aside">
    <el-menu
      text-color="#2173d2"
      active-text-color="#666666"
      >
      <easy-star-menu :nav-menus="menuData" @itemClick="itemClick"></easy-star-menu>
    </el-menu>
  </div>
</template>
<script>
import easymenu from '@/components/docManager/nav/EasyStarMenu'
export default {
  name: 'EasyStarLeft',
  components: {
    EasyStarMenu: easymenu
  },
  data () {
    return {
      classId: '',
      classType: '',
      openeds: ['docManage', 'boxManage', 'roolManage'],
      menuData: []
      // Array: [] // 后台返回的权限数组
    }
  },
  methods: {
    itemClick: function (res) {
      // console.log('点击之后的结果', res)
      let that = this
      var tmpType = '0'
      var select = ''
      var menuName = ''
      switch (res.name.substring(0, 3)) {
        case 'doc':
          tmpType = '0'
          select = this.easyStarAPI.SelectDoc
          if (res.alias === '查看条目') {
            menuName = '文件级管理'
          } else {
            menuName = res.alias
          }
          break
        case 'box':
          tmpType = '1'
          select = this.easyStarAPI.SelectBox
          if (res.alias === '查看条目') {
            menuName = '盒级管理'
          } else {
            menuName = res.alias
          }
          break
        case 'roo':
          tmpType = '2'
          select = this.easyStarAPI.SelectRool
          if (res.alias === '查看条目') {
            menuName = '案卷级管理'
          } else {
            menuName = res.alias
          }
          break
        case 'fil':
          menuName = res.alias
          // res.alias = '原件管理'
          break
        case 'log':
          menuName = res.alias
          // res.alias = '日志管理'
          break
        default:
          that.$message.error('无法读取到文件')
          return false
      }
      if (res.name.substring(0, 3) === 'doc' || res.name.substring(0, 3) === 'box' || res.name.substring(0, 3) === 'roo') {
        that.axios.post(this.api + select).then(function (res1) {
          // console.log('select结果', res1)
          if (res1.data.code === 0) {
            that.axios.get(that.api + that.easyStarAPI.GetOperationRight, {params: {
              mainMenu: menuName
            }}).then(function (res2) {
              if (res2.data.code === 0) {
                // that.Array = res2.data.list
                that.$router.replace({
                  name: res.name,
                  params: {
                    title: res.alias,
                    // classId: this.classId,
                    classType: tmpType,
                    arr: res2.data.list
                  }
                })
              }
            })
          }
        })
      // }
      } else {
        that.axios.get(that.api + that.easyStarAPI.GetOperationRight, {params: {
          mainMenu: menuName
        }}).then(function (res2) {
          console.log('原件、日志的GetOperationRight结果', res2)
        })
      }
    },
    getMenuData: function (classId, classType) {
      let that = this
      this.classId = classId
      this.classType = classType
      this.axios.get(this.api + this.easyStarAPI.ClassPanel, {
        params: {
          classId: this.classId,
          classType: this.classType
        }
      }).then(
        function (res) {
          // console.log('菜单项', res)
          that.menuData = res.data.list
        }
      )
    }
  }
}
</script>
<style>
.aside .el-menu {
  background-color: rgba(255, 255, 255, 0)
}
.el-menu {
  border: 0px;
}
.el-menu-item i{
  color: #2173d2;
}
.el-submenu__title i{
  color: #2173d2;
}
/* .el-menu-item i{
  color: #9d9fa4
}
.el-submenu__title i{
  color: #9d9fa4
} */
</style>



