<template>
  <div id="app">
    <router-view ref="app"/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted () {
    // 注：window.onresize只能在项目内触发1次
    let that = this
    window.onresize = function windowResize () {
      // 通过捕获系统的onresize事件触发我们需要执行的事件
      if (that.$refs['app'] && that.$refs['app'].windowResize) {
        that.$refs['app'].windowResize()
      }
    }
  }
}
</script>

<style>
html,body{
  height:100%;
  width: 100%;
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 0px;
  width: 100%;
  height: 100%;
}
</style>



