export default [
  {
    path: '*',
    component: (resolve) => require(['@/page/login/index.vue'], resolve)
  },
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    name: 'login',
    component: (resolve) => require(['@/page/login/index.vue'], resolve)
  },
  {
    path: '/archivelist',
    name: 'archivelist',
    component: (resolve) => require(['@/page/archivelist/index.vue'], resolve)
  },
  {
    path: '/tablelist',
    name: 'tablelist',
    redirect: {name: 'archive', props: true}
  },
  {
    path: '/changePassword',
    name: 'changePassword',
    component: (resolve) => require(['@/page/archivelist/changepsd.vue'], resolve),
    props: true
  }
]



