import Vue from 'vue'
import Router from 'vue-router'
import LoginRoute from './login/LoginRoute'
import DocManagerRoute from './docManager/DocManagerRoute'
import PermissionManager from './permissionManager/PermissionManager'

Vue.use(Router)

let routes = new Set([...LoginRoute, ...DocManagerRoute, ...PermissionManager])

export default new Router({
  routes,
  mode: 'history'
})



