<template>
  <div class="EasyStarHeader">
      <el-row :gutter="24" justify="start" type="flex">
          <el-col :span="2" style="padding:0px; min-width: 160px!important;">
           <div class="img" style="width:100%; height:60px!important;"><img src="@/assets/images/jylogo.png" style="margin-top:0px;"></div>
          </el-col>
          <el-col :span="3" style="color:#fff; font-size:12px; min-width: 210px!important;">欢迎使用今易档案管理系统</el-col>
          <el-col :span="2">
            <div class="logo">{{title}}</div>
            <!-- <div class="logo" v-if="typeof(this.$route.params.title)=='' || typeof(this.$route.params.title)=='undefined'">{{choosetitle}}</div>
            <div class="logo" v-if="typeof(this.$route.params.title)!='' || typeof(this.$route.params.title)!='undefined'">{{this.$route.params.title}}</div> -->
          </el-col>
          <el-col :offset="3" :span="10">
          </el-col>
          <el-col :span="7">
            <el-col :span="10">
              <el-input class="search"
                placeholder="请输入查询内容"
                title="请输入查询内容"
                clearable>
                <i slot="suffix" class="iconfont icon-sousuo1 header_searchIcon" style="cursor: pointer;"></i>
              </el-input>
            </el-col>
            <el-col :span="4">
              <span style="cursor: pointer;"><i class="iconfont icon-xiaoxi header_message"></i></span>
            </el-col>
            <el-col :span="3">
              <span style="cursor: pointer; margin-left: 15px;"><i class="iconfont icon-dingbudaohang-zhangh header_exit" @click="backToHome"></i></span>
            </el-col>
            <!-- <el-button @click="backToHome" size="mini" title="退出">退出</el-button> -->
          </el-col>
      </el-row>
  </div>
</template>
<script>
export default {
  name: 'EasyStarHeader',
  props: ['choosetitle', 'title'],
  data () {
    return {
      state3: '',
      imgUrl: require('@/assets/logo.png')
    }
  },
  methods: {
    backToHome () {
      this.$router.replace(
        {
          name: 'archivelist'
        }
      )
    }
  }
}
</script>
<style>
  .logo {
    position: absolute;
    font-size: 12px;
    font-weight:600;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-shadow:#fff 1px 0 0,#fff 0 1px 0,#fff -1px 0 0,#fff 0 -1px 0;
    -webkit-text-shadow:#fff 1px 0 0,#fff 0 1px 0,#fff -1px 0 0,#fff 0 -1px 0;
    -moz-text-shadow:#fff 1px 0 0,#fff 0 1px 0,#fff -1px 0 0,#fff 0 -1px 0;
    text-align: center;
    color: #2c3e50;
  }
</style>
<style scoped>
  i{
        line-height: 60px;
    }
  li {
      line-height: normal;
      padding: 7px;
    }
    .name {
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .addr {
      font-size: 12px;
      color: #b4b4b4;
    }
    .highlighted .addr {
      color: #ddd;
    }
    img{
        width: 40px;
        height: 40px;
        margin-top: 10px;
        border-radius: 25px;
    }
    .iconfont{
        font-size: 25px;
    }
    .el-button--small{
        padding: 0;
        background: #b3c0d1;
        border: 0;
    }
    .EasyStarHeader{
      height:100%;
      /* line-height:83px;
      color: #000!important; */
    }
    .el-row{
      margin-bottom:0;
      height:100%;
    }
    .EasyStarHeader img{
      width:100%;
      height:60px!important;
      margin-top:17px;
    }
    .EasyStarHeader .el-col-6{
      text-align:left;
    }
    /* .EasyStarHeader .el-input__inner {
      border-radius: 20px;
    } */
    .EasyStarHeader .search {
      width: 115%!important;
      visibility: hidden;
    }
    .EasyStarHeader .search .el-input__suffix {
      right: 42px;
    }
    .header_message, .header_exit {
      color:#fff;
      font-size:22px;
    }
    .header_searchIcon {
      color:#2173d2!important;
      font-size:22px;
    }
</style>



