<template>
  <div id="easystarmain" style="height: 100%;">
    <el-container style="height: 100%;">
      <el-header>
        <easy-star-header :title="title"></easy-star-header>
      </el-header>
      <el-main>
        <el-container style="height: 100%;">
          <el-aside width="170px" style="overflow-x: hidden;">
            <easy-star-left ref="navleft"></easy-star-left>
          </el-aside>
          <el-main>
            <router-view ref="docMain"/>
          </el-main>
        </el-container>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'EasyStarMain',
  data () {
    return {
      title: '',
      classId: '',
      classType: ''
    }
  },
  created: function () {
    if (sessionStorage.getItem('classId') !== 'undefined' || sessionStorage.getItem('classId') !== 'undefined') {
      this.classId = sessionStorage.getItem('classId')
      this.classType = sessionStorage.getItem('classType')
      this.title = sessionStorage.getItem('title')
    } else {
      this.$router.push({
        name: 'archiveHome'
      })
    }
  },
  mounted: function () {
    this.classId = this.classId
    this.classType = this.classType
    this.title = this.title
    this.$refs['navleft'].getMenuData(this.classId, this.classType)
    this.$nextTick(function () {
      this.$router.replace({
        name: 'archiveHome'
      })
    })
  },
  methods: {
    windowResize: function () {
      if (this.$refs['docMain'] && this.$refs['docMain'].windowResize) {
        this.$refs['docMain'].windowResize()
      }
    }
  }
}
</script>

<style>
#easystarmain .el-header {
  background-image: url("/static/assets/images/top-pic.jpg")!important;
  background-size: cover!important;
}
.el-header, .el-footer {
  background-color: #409EFF;
  color: #333;
  text-align: center;
  line-height: 60px;
}
.el-aside {
  background-image: url("/static/assets/images/pic.jpg");
  background-size: cover;
  /* background-color: rgb(54, 145, 236); */
  color: #333;
  text-align: left;
  line-height: 100%;
  height: 100%;
}
#easystarmain .el-main {
  background-color: rgb(255, 255, 255)!important;
  color: #333!important;
  width: 100%!important;
  height: 100%!important;
  padding: 0!important;
}
</style>



