export default [
  {
    path: '/permission',
    name: 'permission',
    component: resolve => require(['@/page/permission/index.vue'], resolve),
    props: true,
    children: [
      {
        path: 'permissionRoleTable',
        name: 'permissionRoleTable',
        component: resolve =>
          require([
            '@/components/perssionPage/PermissionRoleTable.vue'
          ], resolve)
      },
      {
        path: 'permissionUserTable',
        name: 'permissionUserTable',
        component: resolve =>
          require([
            '@/components/perssionPage/PermissionUserTable.vue'
          ], resolve)
      }
    ]
  }
]



